<template>
    <div>
        <article id="ace-card" class="ace-card bg-primary">
        <div class="ace-card-inner text-center">
            <img class="avatar avatar-195" src="@/styles/img/personal.jpg" width="195" height="195" alt="">
            <h1>{{ site.name }}</h1>
            <p class="text-muted">{{ site.position }}</p>
            <ul class="ace-social clear-list">
            <li><a :href="site.linkedin" :title="site.linkedin" target="_blank"><span class="ace-icon ace-icon-linkedin"></span></a></li>
            <li><a :href="'mailto:' + site.email" :title="site.email"><span class="ace-icon ace-icon-contact"></span></a></li>
            </ul>
        </div>
        <footer class="ace-card-footer">
            <a class="btn btn-lg btn-block btn-thin btn-upper" href="javascript:void(0)" onclick="window.print()">Télécharger CV</a>
        </footer>
        </article>
    </div>
</template>
<script>
import Vue from 'vue'
import site from '@/var/global.vue'
Vue.prototype.site = site

export default({

})
</script>



