
<template>
    <div lang="en" class="ace ace-card-on ace-tab-nav-on ace-main-nav-on ace-sidebar-on" data-theme-color="#c0e3e7">
        <hd/>
        <div class="ace-wrapper">
            <personal/>
            <div id="ace-content" class="ace-container-shift">
                <div class="ace-container">
                    <navigation/>
                    <div class="ace-paper-stock">
                        <main class="ace-paper clearfix">
                            <div class="ace-paper-cont clear-mrg">
                                <!-- START: PAGE CONTENT -->
                                <div class="padd-box clear-mrg">

                                <basic/>
                                <profetional/>
                                <education/>
                                <experience_pro/>
                                <experience_per/>
                               
                                </div><!-- .padd-box -->
                            <!-- END: PAGE CONTENT -->
                            </div><!-- .ace-paper-cont -->
                        </main><!-- .ace-paper -->
                    </div><!-- .ace-paper-stock -->
                </div><!-- .ace-container -->
            </div><!-- #ace-content -->
            <ft/>
        </div><!-- #ace-wrapper -->
    </div>
</template>

<script>
import '@/styles/js/vendor/jquery-1.12.4.min.js' 

import '@/styles/js/options.js'
import '@/styles/js/main.js'

import basic from '@/components/basic.vue'
import hd from '@/components/head.vue'
import navigation from '@/components/navigation.vue'
import education from '@/components/education.vue'
import experience_pro from '@/components/experience_pro.vue'
import experience_per from '@/components/experience_personal.vue'
import ft from '@/components/footer.vue'
import personal from '@/components/personal.vue'
import profetional from '@/components/profetional.vue'


export default{
    components: {basic,hd,navigation,education,experience_pro,experience_per,ft,personal,profetional}
}
</script>




